.qrCode {
	position: relative;
	display: flex;
	flex-direction: column;
	width: 100%;
	filter: drop-shadow(0px 0px 10px rgba(18, 18, 18, 0.08));
}

.qrCode__header {
	position: relative;
	background-color: #fff;
	padding: 1rem;
	padding-bottom: 0;
	font-size: 1.2rem;
	font-weight: 600;
	border-radius: 16px 16px 0 0;

}

.qrCode__header::after {
	position: absolute;
	content: "";
	height: 1px;
	border-top: 2px dashed #eee;
	left: 20px;
	right: 20px;
	bottom: -15px;
	z-index: 2;
}

.qrCode__rip {
	position: relative;
	height: 30px;
	flex-shrink: 0;
	margin: 0 15px;
	background-color: #fff;
}

.qrCode__rip::before,
.qrCode__rip::after {
	position: absolute;
	content: "";
	width: 50px;
	height: 50px;
	border: 10px solid transparent;
	border-top-color: #fff;
	border-right-color: #fff;
	transform: translate(-50%, -50%) rotate(45deg);
	border-radius: 50%;
	top: 50%;
}

.qrCode__rip::before {
	left: -15px;
}

.qrCode__rip::after {
	right: -65px;
	transform: translate(-50%, -50%) rotate(225deg);
}

.qrCode__body {
	background-color: #fff;
	padding: 1rem;
	border-radius: 0 0 16px 16px;
}