.box {
  width: 100%;
  height: 100%;
  outline-offset: 10px;
  position: relative;
}
.box::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 2.5%;
  width: 95%;
  height: 1px;
  background: #33cb33;
  box-shadow: 0 0 80px 4px #33cb33;
  clip-path: inset(0);
  animation: x 5s ease-in-out infinite alternate, y 10s ease-in-out infinite;
}

.yellowBox {
  width: 100%;
  height: 100%;
  outline-offset: 10px;
  position: relative;
}
.yellowBox::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 2.5%;
  width: 95%;
  height: 1px;
  background: #fbbc05;
  box-shadow: 0 0 80px 4px #fbbc05;
  clip-path: inset(0);
  animation: x 5s ease-in-out infinite alternate, y 10s ease-in-out infinite;
}

video {
  height: 100%;
  width: unset !important;
  background-color: #444444;
  /* position: absolute; */
  /* left: -50%; */
}

@keyframes x {
  to {
    transform: translateY(-100%);
    top: 100%;
  }
}

@keyframes y {
  33% {
    clip-path: inset(-40px 0 0 0);
  }
  50% {
    clip-path: inset(0 0 0 0);
  }
  83% {
    clip-path: inset(0 0 -40px 0);
  }
}

.MuiTabs-root {
  width: 100%;
}
